<!-- eslint-disable-next-line -->
<!-- eslint-disable -->
<template>
  <div class="home">
        <ModalComp @closeModal="modalOpen = false" :modalOpen="modalOpen" />
        <LocationComp />
        <div class="container">
            
            <div class="row">
                <!-- 썸네일 -->
                <ThumnailComp :product="product" />
                <!-- 상세페이지 내용 -->
                <div class="col-md-7">
                    <div class="card text-center">
                        <div class="card-body">
                          <div  :key="i" v-for="(product, i) in productDetail">
                            <div class="top_ic">
                              <div>
                                <p class="card-text pt-10 border-top">
                                  <span class="badge bg-secondary">{{ product.tags }}</span>
                                </p>
                              </div>
                              <div class="top_ic_list">
                                <font-awesome-icon :icon="['fas','heart']"/>
                                <font-awesome-icon :icon="['fas', 'share-square']" style="margin-left: 10px;" />
                              </div>
                            </div>                         
                            <!-- <font-awesome-icon :icon="['fas', 'star']" /> -->
                            <h5 class="card-title">{{ product.product_name  }}</h5>
                            <h6 >{{ product.product_subtitle }}</h6>
                            <p class="card-text">{{ product.content }}</p>
                          </div>

                          <SizePrice :product="product" />
                          <OrderMenu @updateProduct="updateProduct" />

                          <!-- 구매수량 가격-->
                          <OrderPirce @updateTotal="updateTotal" />
                         
                          <div class="d-flex justify-content-between align-items-center pad">
                            <div class="col-6 d-grid p-1">
                                <button type="button" class="btn btn-outline-dark">장바구니</button>
                            </div>
                            <div class="col-6 d-grid p-1">
                                <button type="button" class="btn btn-outline-danger" @click="productInsert" >주문하기</button>
                            </div>
                          </div>
                          <div class="d-flex align-items-center">
                            <div class="col-12 d-grid p-1">
                                <button type="button" class="btn btn-outline-dark" @click="goToList" >목록</button>
                            </div>
                          </div>
                        </div>
                </div>
            </div>
        </div>
        </div>
  </div>
</template>

<script>
// eslint-disable-next-line
/* eslint-disable */
import { createApp } from 'vue';
import ThumnailComp from '../components/ThumnailComp.vue'
import SizePrice from '../components/SizePrice3.vue'
import OrderPirce from '../components/OrderPrice.vue'
import ModalComp from '../components/ModalComp.vue'
import LocationComp from '../components/LocationComp.vue'
import OrderMenu from '../components/OrderMenu.vue'

const app = createApp({});

export default {
    data() {
      return {
        modalOpen: false,
        product_Id: 0,
        productImage: [],
        productDetail: {},
        product: {
          product_Id: 0,
          product_name: "",
          order_price: 0,
          temp: 0,
          cup:0,
          pickup:0
        },
        totalPrice: 0,
      }
    },
    computed: {
      user(){
        return this.$store.state.user;
      }
    },
    created(){
      this.product_Id = this.$route.query.product_id;
      console.log('this.productId는', this.product_Id)
      this.getProductDetail();
      this.getProductImage();
      console.log('this.getProductDetail실행 결과1', this.getProductDetail)
    },
    methods: {
      goToList() {
        this.$router.push({path:'/list'});
      },
      updateProduct(updatedProduct) { // 주문내용
        this.product = Object.assign({}, this.product, updatedProduct);
      },
      updateTotal(total) { // 총합계
        this.product.order_price = total;
        console.log('가져온 총 합계는', this.product.order_price);
      },
      async getProductDetail(){
        this.productDetail = await this.$api('/api/productDetail', {});
         if (this.productDetail && this.productDetail.length > 0) {
      // productDetail이 배열이고 적어도 하나의 항목이 있는 경우
      this.product = {
        ...this.product,
        ...this.productDetail[0],
      };
         }
      },
    //   async getProductDetail(){
    //     let productDetail = await this.$api('/api/productDetail', {param:[this.productId]});
    //     if(productDetail.length > 0) {
    //       this.productDetail = productDetail[0];
    //     }
    //     console.log(this.productDetail);
    // },
      async getProductImage(){
        this.productImage = await this.$api('/api/productMainImages', {});
        console.log(this.productImage);
      },
      productInsert(){    
        // if (this.product.product_name == ""){
        //   return this.$swal("제품명은 필수 입력값입니다");
        // }
        // if (this.product.product_price == ""){
        //   return this.$swal("가격은 필수 입력값입니다");
        // }
        this.$swal.fire({
          title: '정말 주문 하시겠습니까?',
          showCancelButton: true,
          confirmButtonText: '주문',
          cancelButtonText: '취소',
          }).then(async (result) => {
          if(result.isConfirmed){
            try {
            await this.$api('/api/productInsert', { param: [this.product_id, this.product.product_name, this.product.temp, this.product.cup, this.product.pickup, this.product.order_price] });
            // console.log('this.product 저장된 값은????', this.product);
            this.$swal.fire('주문되었습니다!', '', 'success');
            this.$router.push({path:'/Sales'});      
            } catch (error) {
                console.error('주문 등록 중 에러 발생:', error);
                this.$swal.fire('주문 등록에 실패했습니다.', '', 'error');
            }                     
          }
      });
    },
      getCurrencyFormat(value){ //CurrencyFormat mixins
      return this.$currencyFormat(value);
      }
    },
    props : {
      product : Object,
    },
    components: {
        ThumnailComp,
        SizePrice,
        OrderPirce,
        ModalComp,
        LocationComp,
        OrderMenu
    }
}
</script>

<style scoped>
.top_ic {
  display: flex;
  align-items: baseline;
  flex-direction: row;
  justify-content: space-between;
}

.top_ic_list {
  padding: 10px;
  border-radius: 10px;
  /* border: 1px solid #400bff; */
  background: #eee;
}

.pad {
  padding: 15px 0;
}


</style>