// eslint-disable-next-line
/* eslint-disable */
import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import mixins from './mixins'
import store from './store'; // store import 추가
import { library } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import { faHeart, faShareSquare, faArrowRight, faMugSaucer, faShoePrints, faMotorcycle, faLocationDot, faCartShopping, faStar  } from '@fortawesome/free-solid-svg-icons';
import mitt from 'mitt'
import VueSweetalert2 from 'vue-sweetalert2';
import 'sweetalert2/dist/sweetalert2.min.css';

// Vue 객체를 생성
const app = createApp(App);

// 전역 컴포넌트 및 라이브러리 등록
app.component('font-awesome-icon', FontAwesomeIcon);
library.add(faHeart, faShareSquare, faArrowRight, faMugSaucer, faShoePrints, faMotorcycle, faLocationDot, faCartShopping, faStar );

// mitt
const emitter = mitt();
app.config.globalProperties.emitter = emitter;

// 라우터 및 앱 마운트
// store 등록  추가
app.use(router).use(store).use(VueSweetalert2).mixin(mixins)
app.mount('#app');
