import { createWebHistory, createRouter } from "vue-router";
import HomeView from './views/HomeView.vue'
import List from './views/ListView';
import detail from './views/DetailView.vue'
import Sales from './views/Sales.vue'
import Update from './views/productUpdate.vue'
import image_insert from './views/imageInsert.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: "/list",
    component: List
  },
  {
    path: '/detail',
    name: 'detail',
    component: detail
  },
  {
    path: '/Sales',
    name: 'Sales',
    component: Sales
  },
  {
    path: '/Update',
    name: 'Update',
    component: Update
  },
  {
    path: '/image_insert',
    name: 'image_insert',
    component: image_insert
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router; 