// eslint-disable-next-line
/* eslint-disable */

import { createStore } from 'vuex'
import persistedstate from 'vuex-persistedstate' // persistedstate 설치 

const store = createStore({
  state() {
    return {
      user: {} //메모리 저장 공간 할당
    }
  },
  mutations: {
    user(state, data){
      state.user = data;
    }
  },
  plugin: [
    persistedstate({ //새로고침해도 로그아웃 안됨
      paths: ['user'] //유지시키고 싶은 state값 
    })
  ]
});

export default store;